.nft {
  width: 100%;
  height: auto;
  background-color: transparent;
  border-radius: 7px;
  box-shadow: 0 0 0 1px #012a57, 0 3px 0 #012a57;
}

.nft img {
  display: inline-block;
}

.nft2 {
  width: 100%;
  height: auto;
  background-color: transparent;
  border: none;
  
}

.top { 
  display: inline-block;
 
  width: 100%;
  height: auto;
  border-radius: 7px 7px 0px 0px;
  margin: 0;
  padding: 0;
}

.down {
  display: inline-block;

  width: 100%;
  height: 2.6em;
  margin: 0;
  padding: 0;
  align-items: center;
  border-radius: 0px 0px 7px 7px;
  font-family: 'Fredoka One', cursive;
  
  text-shadow: 2px 1px 0 #012a57, -1px -1px 0 #012a57, 1px -1px 0 #012a57, -1px 1px 0 #012a57, 1px 1px 0 #012a57;
  color: white;


}

.down .effect {
  width: 100%;
  display: inline-block;
  border-radius: 0px 0px 7px 7px;
  
}

.down p{
  padding: 0.47em;
}


